.nav {
  background-color: black;
  display: flex;
  gap: 30px;
  flex-direction: column;
  width: 20%;
  height: 100%;
  border-right: 1px solid grey;
}

.genres {
  display: flex;
  justify-content: center;
  font-size: 30px;
}

.btn {
  background-color: black;
  height: 50px;
  font-size: 20px;
}

.btn:hover {
  background-color: rgb(231, 231, 231);
  cursor: pointer;
  transition: 1s;
}

.btn:focus {
  background-color: rgb(231, 231, 231);
}/*# sourceMappingURL=navbar.css.map */